.cash-withdraw-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #f0f4f8;
    padding: 20px;
    border-radius: 10px;
    width: 300px;
    margin: auto;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .header {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
  }
  
  .name {
    font-size: 24px;
    font-weight: bold;
    color: #333;
  }
  
  .initials {
    font-size: 18px;
    color: #777;
  }
  
  .balance-container {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .balance-text {
    font-size: 18px;
    color: #333;
  }
  
  .balance-amount {
    font-size: 24px;
    font-weight: bold;
    color: #2d87bb;
  }
  
  .currency {
    font-size: 14px;
    color: #777;
  }
  
  .denomination-list {
    display: flex;
    justify-content: space-around;
    width: 100%;
  }
  
  .denomination-button {
    background-color: #2d87bb;
    color: white;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .denomination-button:hover {
    background-color: #1c5d8c;
  }
  